.workflow-container {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding: 20px;
  overflow-y: auto;
}

.phase-row {
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  background: #f2f2fc;
  gap: 0px;
  padding-bottom: 20px;
  height: auto; /* Make height flexible */
  transition: height 0.3s ease-in-out; /* Smooth transition */
  overflow: auto; /* Prevent sudden overflow issues */

}

.phase-label-container {
  position: relative;  
  display: flex;
  flex-direction: row;
  justify-content: center;
  

  top: 10px; /* Move above the subtasks */
  left: 10px;
  background-color: transparent;
  padding: 5px 0px;
  border-radius: 5px;
  z-index: 1;
}

.phase-label-text {
  color: #000;
  text-align: center;
  font-family: "Noto Sans";
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%; /* 24px */
  letter-spacing: -0.304px;
}

.phase-hover-area {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.02);
  pointer-events: none; /* Ensure it doesn't block interactions */
  transition: background-color 0.3s;
  z-index: -1;
}

.phase-row:hover .phase-hover-area {
  background-color: rgba(0, 0, 0, 0.05);
}

.subtasks-wrapper {
  display: flex;
  padding: 26px 17px;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 10px;
}

.subtask {
  flex: 1 1 250px;
  border-radius: 15px;
  background: #fff5f5;
  box-shadow: 2px 2px 15px 0px rgba(0, 0, 0, 0.15);
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 8px;
  transition: box-shadow 0.3s, transform 0.1s;
  cursor: pointer;
}

.subtask:hover {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transform: scale(1.02);
}

.subtask-title {
  font-size: 1.2rem;
  margin-bottom: 5px;
}

.subtask-description {
  font-size: 1rem;
  color: #666;
  margin-bottom: 10px;
}

.steps {
  display: inline-flex;
  padding: 26px 17px;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.step {
  display: flex;
  height: auto;
  width: 100%;
  padding: 6.508px 8px 7.492px 8px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-self: stretch;
  border-radius: 5px;
  background:  #fff5f5;
  color: rgb(0, 0, 0);
}

.step:hover {
  background: transparent;
}
.step-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px;
  background-color: #ffffff;
  border-radius: 8px;
  cursor: pointer;
}

.step-roleCircle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: var(--profile, linear-gradient(143deg, #75B8FF 19.35%, #FFE3B8 91.44%));  
  color: white;
  font-weight: bold;
  font-size: 14px;
}

.step-indexCircle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: var(--profile, linear-gradient(143deg, #fdfeff 19.35%, #ffffff 91.44%));  
  color: rgb(2, 2, 2);
  font-weight: bold;
  font-size: 14px;
}

.step-index {
  background-color: #6c757d;
}

.step-role {
  background-color: #28a745;
}

.step-name {
  flex: 1;
  text-align: center;
  font-weight: bold;
  color: #333;
  padding: 0 10px;
}

.step-details {
  margin-top: 10px;
  padding: 10px;
  background-color: #fff;
  border-radius: 5px;
}

.step-attribute {
  margin-bottom: 5px;
}
